<template>
    <div class="wrapper">
        <el-row>
            <el-button type="primary" @click="add">新增</el-button>
        </el-row>
        <div class="tableCon">
            <template>
                <el-table
                    :data="save.tableData"
                    style="width: 100%; border: 1px solid #eee;border-bottom: none;">
                    <el-table-column
                    label="人员名称"
                    width="280">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.name }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                        label="角色"
                        width="280">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.title }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            @click="edit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="del(scope.$index, scope.row)">删除</el-button>
                    </template>
                    </el-table-column>
                </el-table>
            </template>
            <div class="fenye">
                <el-pagination
                    @current-change="changePage"
                    :current-page="currentPage"
                    background
                    layout="prev, pager, next"
                    :total="count">
                </el-pagination>
            </div>
        </div>

        <!-- alert -->
        <div class="alert_bg" v-show="save.status"></div>
        <div class="alert-con" v-show="save.status">
            <div class="title">新增</div>
            <div class="con">
                <div class="item">
                    <p>人员名称：</p>
                    <el-input v-model="save.name" placeholder="请输入内容"></el-input>
                </div>
                <div class="item">
                    <p>密码：</p>
                    <el-input placeholder="请输入密码" v-model="save.apwd" show-password></el-input>
                </div>
            </div>
            <el-row class="_btn">
                <el-button size="mini" @click="close">取消</el-button>
                <el-button size="mini" type="primary" @click="sub">确认</el-button>
            </el-row>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data () {
        return {
            count: 0,
            currentPage: 1,
            save: {
                tableData: [],
                status: false, // add edit
                name: '',
                apwd: '',
                id: '',
            }
        }
    },
    methods: {
        // 切换页面
        changePage(val) {
            console.log(val)
            this.getAdministrator(val - 1)
        },
        // 获取列表
        getAdministrator(pageNum) {
            api.getAdministrator(pageNum, 10).then(res => {
                console.log(res);
                console.log(res.data.array);
                this.save.tableData = res.data.array;
                this.count = res.data.listconut;
            })
        },
        // 打开修改
        edit(index, val) {
            console.log(val)
            this.save.status = "edit";
            this.save.name = val.name;
            this.save.id = val.id;
            this.save.apwd = "";
        },
        // 打开新增
        add() {
            this.save.status = "add";
            this.save.name = "";
            this.save.apwd ="";
        },
        // 取消
        close() {
            this.save.status = false;
        },
        // 删除
        del(index, val) {
            console.log(val)
            this.$confirm('此操作将永久删除该线路, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                api.delAdministrator(val.id).then(res => {
                    console.log(res)
                    if(res.data.code == 200) {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                        this.getAdministrator(0, 10);
                    } else {
                        this.$message({
                            type: 'info',
                            message: '删除失败'
                        });  
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        // 提交判断
        sub() {
            switch(this.save.status) {
                case "add":
                    this.newAdministrator();
                    break;
                case "edit":
                    this.changeAdministrator();
                    break;
            }
        },
        // 新增
        newAdministrator() {
            api.newAdministrator({
                jurisdiction: "高级",
                ausername: this.save.name,
                apwd: this.save.apwd
            }).then(res => {
                console.log(res);
                if(res.data.code == 200) {
                    this.$message({
                        type: 'success',
                        message: '新增成功！'
                    });
                    this.getAdministrator(0, 10);
                } else {
                    this.$message({
                        type: 'warning',
                        message: '新增失败！'
                    });
                }
                this.save.status = false;
            })
        },
        // 修改
        changeAdministrator() {
            api.changeAdministrator({
                jurisdiction: '高级',
                ausername: this.save.name,
                apwd: this.save.apwd,
                aid: this.save.id
            }).then(res => {
                console.log(res);
                if(res.data > 0) {
                    this.$message({
                        type: 'success',
                        message: '修改成功！'
                    });
                    this.getAdministrator(0, 10);
                } else {
                    this.$message({
                        type: 'warning',
                        message: '修改失败！'
                    });
                }
                this.save.status = false;
            })
        }
    },
    created() {
        this.getAdministrator(0);
    },
}
</script>
<style lang="stylus" scoped>
.tableCon
    border-radius 12px
    overflow hidden
    margin-top 12px
    background #fff
    padding 20px
    .fenye
        padding-top 10px
        text-align center
.alert_bg
    width 100%
    height 100vh
    background rgba(0,0,0,.5)
    position fixed
    left 0
    top 0
    z-index 900
.alert-con
    position fixed
    left 50%
    top 50%
    transform translate(-50%, -50%)
    z-index 999
    width 300px
    background #fff
    border-radius 4px
    padding 20px
    .title 
        padding-bottom 20px
    .con
        .item
            display flex
            align-items center
            white-space nowrap
            padding 4px 0
            p 
                width 120px
    ._btn
        display flex
        justify-content flex-end
        padding-top 20px
</style>



